.App {
  text-align: center;
}


.column {
  text-align: center;
  float: left;
  width: 50%;
  padding: 10px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
    font-family: Arial;
    color: white;
  }
  
  h2,h3,h4,h5{
    font-family: 'Rajdhani', ;
    font-size: 30px;
    margin-top: 4px;
    color:whitesmoke;
  }

  .iconImg{
  max-width: 480px;
  -webkit-transition: all .2s ease-in-out;
    
  }
  .iconImg:hover {
    -webkit-transform: scale(1.4);
}

  .split {
    height: 100%;
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 0;
    overflow-x: hidden;
    padding-top: 20px;

  }

 
  .left {
    left: 0;
    background-color:#264653;
    
  }
  
  .right {
    right: 0;
    background-color: rgb(66, 112, 107);
  }
  
  .centered {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .centered img {
    width: 1150px;
    
  }
  .welcomeImg{
    max-width:450px;
    height: 300px;
  }


  
